/** @format */

import React, { useState } from "react";

const Box = (props) => {
    const [firstClick, setFirstClick] = useState(true);
    const [text, setText] = useState("");

    const doNothing = () => {};

    const handleClick = () => {
        setFirstClick(false);
        renderData();
        props.turnMethod();
        props.boxClicked(props.css);
    };

    const renderData = () => {
        if (firstClick) {
            if (props.turn % 2 !== 0) {
                setText("X");
            } else if (props.turn % 2 === 0) {
                setText("O");
            }
        }
    };

    return (
        <>
            <div
                onClick={firstClick ? handleClick : doNothing}
                className={props.css + " " + text}
            >
                {text}
            </div>
        </>
    );
};

export default Box;
