/** @format */

import React, { useEffect, useState } from "react";
import Box from "./Box";
import "./App.css";

function App() {
    const [turn, setTurn] = useState(1);
    const [over, setOver] = useState(false);
    const [draw, setDraw] = useState(false);
    const [winner, setWinner] = useState(new Array(9).fill(null));

    useEffect(() => {
        if (
            winner[0] === winner[1] &&
            winner[0] === winner[2] &&
            winner[0] !== null &&
            winner[1] !== null &&
            winner[2] !== null
        ) {
            setOver(true);
        } else if (
            winner[3] === winner[4] &&
            winner[3] === winner[5] &&
            winner[3] !== null &&
            winner[4] !== null &&
            winner[5] !== null
        ) {
            setOver(true);
        } else if (
            winner[6] === winner[7] &&
            winner[6] === winner[8] &&
            winner[6] !== null &&
            winner[7] !== null &&
            winner[8] !== null
        ) {
            setOver(true);
        } else if (
            winner[0] === winner[3] &&
            winner[0] === winner[6] &&
            winner[0] !== null &&
            winner[3] !== null &&
            winner[6] !== null
        ) {
            setOver(true);
        } else if (
            winner[1] === winner[4] &&
            winner[1] === winner[7] &&
            winner[1] !== null &&
            winner[4] !== null &&
            winner[7] !== null
        ) {
            setOver(true);
        } else if (
            winner[2] === winner[5] &&
            winner[2] === winner[8] &&
            winner[2] !== null &&
            winner[5] !== null &&
            winner[8] !== null
        ) {
            setOver(true);
        } else if (
            winner[0] === winner[4] &&
            winner[0] === winner[8] &&
            winner[0] !== null &&
            winner[4] !== null &&
            winner[8] !== null
        ) {
            setOver(true);
        } else if (
            winner[2] === winner[4] &&
            winner[2] === winner[6] &&
            winner[2] !== null &&
            winner[4] !== null &&
            winner[6] !== null
        ) {
            setOver(true);
        } else if (
            winner[0] !== null &&
            winner[1] !== null &&
            winner[2] !== null &&
            winner[3] !== null &&
            winner[4] !== null &&
            winner[5] !== null &&
            winner[6] !== null &&
            winner[7] !== null &&
            winner[8] !== null
        ) {
            console.log("Done");
            setDraw(true);
            setOver(true);
        }
    });

    const whatBoxClicked = (boxNo) => {
        setWinner(() => {
            let str = boxNo[3];
            let temp = winner;
            if (turn % 2 === 0) {
                temp[parseInt(str) - 1] = 1;
            } else {
                temp[parseInt(str) - 1] = 2;
            }
            return temp;
        });
    };

    const handleTurn = () => {
        setTurn(turn + 1);
    };

    return (
        <>
            <div className="container">
                {over ? (
                    <>
                        <div className="final">
                            <h6 className="final_text">
                                {draw
                                    ? "Match Draw"
                                    : turn % 2 === 0
                                    ? "X is Winner"
                                    : "O is Winner"}
                            </h6>
                            <button
                                className="final_button"
                                onClick={() => {
                                    setWinner(new Array(9).fill(null));
                                    setOver(false);
                                }}
                            >
                                Restart
                            </button>
                        </div>
                    </>
                ) : (
                    <div className="outer-container">
                        <div className="inner-container">
                            <Box
                                css="box1"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                            <Box
                                css="box2"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                            <Box
                                css="box3"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                        </div>
                        <div className="inner-container">
                            <Box
                                css="box4"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                            <Box
                                css="box5"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                            <Box
                                css="box6"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                        </div>
                        <div className="inner-container">
                            <Box
                                css="box7"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                            <Box
                                css="box8"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                            <Box
                                css="box9"
                                turn={turn}
                                turnMethod={handleTurn}
                                boxClicked={whatBoxClicked}
                            />
                        </div>
                    </div>
                )}
            </div>
        </>
    );
}

export default App;
